* {
  box-sizing: border-box;
}

body{
  font-family: "Karla", sans-serif;
}

main{
  padding: 36px;
}

.header{
  display: flex;
  align-items: center;
  height: 75px;
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  color: white;
  padding: 20px;
  justify-content: center;
}

.header-image{
  height: 100%;
  margin-right: 6px;
}

.header-title{
  font-size: 1.25 rem;
}

.form{
  display: grid;
  grid-template: 40px 40px / 1fr 1fr;
  gap: 17px;
  margin-bottom: 17px;
  justify-content: center;
}

.form-input{
  font-family: "Karla",sans-serif;
  border-radius: 5px;
  border: 1px solid #D5D4D8;
  text-indent: 5px;
}

.form-button{
  grid-column: 1 / -1;
  font-family: "Karla",sans-serif;
  border-radius: 5px;
  background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
  color: white;
  border: none;
  cursor: pointer;
}

.meme{
  position: relative;
  text-align: center;
}

.meme-image{
  border-radius: 3px;
}

.meme-text{
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 15px;
  font-family: impact,sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom{
  bottom: 0;
}

.top{
  top: 0;
}

